<!--
 * @Descripttion: vxe-table表格封装组件
 * @version:
 * @Author: YZZ
 * @Date: 2020-03-26 15:43:07
 * @LastEditors: ZJM
 * @LastEditTime: 2022-09-22 09:45:47
 -->
<template>
  <div>
    <vxe-grid
      class="vxe-table-element"
      :ref="tableName+'Table'"
      :row-style="rowStyle"
      :header-row-style="headerStyle"
      :resizable="resizable"
      :border="border"
      :show-header="showHeader"
      :auto-resize="autoResize"
      :header-align="headerAlign"
      :show-footer="showFooter"
      :show-footer-overflow="showFooterOverflow"
      :show-overflow="showOverflow"
      :stripe="stripe"
      :max-height="maxHeight"
      :height="height"
      :size="size"
      :remote-sort="true"
      :highlight-hover-row="highlightHoverRow"
      :highlight-current-row="highlightCurrentRow"
      :loading="tableLoading"
      :tooltip-config="tooltipConfig"
      :keep-source="keepSource"
      :edit-config="editConfig"
      :columns="tableColumns"
      :data="tableDatas"
      :checkbox-config="checkboxConfig"
      :radio-config="{trigger: 'row'}"
      :seq-config="{startIndex: (tablePage.currentPage - 1) * tablePage.pageSize}"
      :pager-config="tablePage"
      :span-method="mergeRowMethod"
      :export-config="{ cellRender: handleCellRender }"
      @cell-click="cellClickEvent"
      @cell-dblclick="cellDBLClickEvent"
      @page-change="handlePageChange"
      @sort-change="sortChange"
      @checkbox-change="checkboxChangeEvent"
      @checkbox-all="checkboxChangeEvent"
      @current-change="currentChangeEvent"
    >
      <!--自定义插槽 pager_left-->
      <template v-slot:pager_left >
        <div class="page-left">
          <!-- vxe-pager是vxe自带样式： 字体大小14，高度48像素 -->
          <!-- <div class="tableAllCheck vxe-pager">
            <vxe-checkbox
              v-model="isAllChecked"
              :indeterminate="isIndeterminate"
              @change="changeAllEvent"
            ></vxe-checkbox>
          </div>
          <div>
            <span class="select-count">已选中 {{ selectRecords.length }} 条</span>
          </div> -->
          <div>
            <slot name="tableOperation"></slot>
          </div>
        </div>
      </template>
    </vxe-grid>
    <!-- <h2>{{tablePage}}</h2> -->
  </div>
</template>

<script>
export default {
  props: {
    rowStyle: {
      type: Function
    },
    headerStyle: {
      type: Function
    },
    resizable: {
      type: Boolean,
      default: true
    },
    tableName: {
      type: String,
      required: true
    },
    showHeader: {
      type: Boolean,
      default: true
    },
    // 边框
    // 参数：true（有边框），false（无边框）,default（默认）, full（完整边框）, outer（外边框）, inner（内边框）, none（无边框）
    border: {
      type: [Boolean, String],
      default: true
    },
    // 自动监听父元素响应式调整大小
    // 参数：true，false
    autoResize: {
      type: Boolean,
      default: true
    },
    // 表头对齐方式
    // 参数： left（左对齐）, center（居中对齐）, right（右对齐）
    headerAlign: {
      type: String,
      default: 'center'
    },
    // 是否显示表尾
    // 参数：true，false
    showFooter: {
      type: Boolean,
      default: false
    },
    // 设置表尾所有内容过长时显示为省略号
    // ellipsis（只显示省略号）,title（并且显示为原生 title）,tooltip（并且显示为 tooltip 提示）
    showFooterOverflow: {
      type: [Boolean, String],
      default: 'tooltip'
    },
    // 设置所有内容过长时显示为省略号
    showOverflow: {
      type: [Boolean, String],
      default: 'tooltip'
    },
    // 是否带有斑马纹
    // 参数：true，false
    stripe: {
      type: Boolean,
      default: true
    },
    // 表格最大高度
    // 参数：%，px
    maxHeight: {
      type: [Number, String],
      default: ''
    },
    height: {
      type: [Number, String],
      default: '500px'
    },
    // 表格尺寸
    // 参数：
    size: {
      type: String,
      default: 'small'
    },
    // 鼠标移到行是否要高亮显示
    // 参数：true，false
    highlightHoverRow: {
      type: Boolean,
      default: true
    },
    // 鼠标点击行是否要高亮显示
    // 参数：true，false
    highlightCurrentRow: {
      type: Boolean,
      default: false
    },
    // 表格是否显示加载中
    // 参数：true，false
    tableLoading: {
      type: Boolean,
      default: false
    },
    // tooltip 配置项
    // 参数：theme(主题)，参数：dark，light;enterable(鼠标是否可进入到 tooltip 中);leaveDelay(鼠标移出后延时多少才隐藏 tooltip)
    tooltipConfig: {
      type: Object,
      default: () => {
        return {
          theme: 'light',
          enterable: true
        };
      }
    },
    checkboxConfig: {
      type: Object,
      default: () => {
        return {};
      }
    },
    exportConfig: {
      type: Object,
      default: () => {
        return {};
      }
    },
    // 保持原始值的状态
    // 参数：true，false
    keepSource: {
      type: Boolean,
      default: false
    },
    // 可编辑配置项(配合keepSource一起使用)
    editConfig: {
      type: [Boolean, Object],
      default: false
    },
    // 表格数据
    tableDatas: {
      type: Array,
      required: true
    },
    // // 合并数据
    // mergeArr: {
    //   type: Array,
    //   default: () => {
    //     return [];
    //   }
    // },
    // 表格列配置
    tableColumns: {
      type: Array,
      required: true
    },
    // 分页配置
    tablePage: {
      type: Object,
      required: true
    },
    checkRecords: {
      type: Array,
      default: () => {
        return [];
      }
    }
    /* ,
    isAllChecked: {
      type: Boolean,
      default: false
    },
    isIndeterminate: {
      type: Boolean,
      default: false
    },
    selectRecords: {
      type: Object,
      default: () => {
        return {};
      }
    } */
  },
  data() {
    return {
      isAllChecked: false,
      isIndeterminate: false,
      selectRecords: [],
      mergeArr: []
    };
  },
  watch: {
    checkRecords(val, oldVal) {
      if (val.length === 0) {
        this.checkboxChangeEvent([]);
      }
    }
  },
  methods: {
    handleCellRender({ row, column, cellValue }) {
      // 在这里根据条件设置单元格样式
      console.log(666, row, column, cellValue);
      return {
        style: 'color: red;'
      };
    },
    /**
     * @description: 分页改变事件
     * @param {Number} currentPage 第几行
     * @param {Number} pageSize 每页显示行数
     * @return:
     * @author: YZZ
     */
    handlePageChange({ currentPage, pageSize }) {
      this.isAllChecked = false;
      this.selectRecords = [];
      this.$emit('onTablePageChange', currentPage, pageSize);
    },
    /**
     * @description: 排序改变事件
     * @param {Object} column 点击的表头数据
     * @param {String} property 字段名
     * @param {String} order 排序方式：asc，desc
     * @return:
     * @author: YZZ
     */
    sortChange({ column, property, order }) {
      console.log(666);
      this.isAllChecked = false;
      this.selectRecords = [];
      this.$emit('sortChange', property, order);
    },
    /**
     * @description: 表格勾选方法
     * @param {Objcet} records 勾选数据
     * @return:
     * @author: YZZ
     */
    checkboxChangeEvent({ records }) {
      this.isAllChecked = this.$refs[
        this.tableName + 'Table'
      ].isAllCheckboxChecked();
      this.isIndeterminate = this.$refs[
        this.tableName + 'Table'
      ].isCheckboxIndeterminate();
      if (records) {
        this.selectRecords = records;
      } else {
        this.selectRecords = [];
        this.isIndeterminate = false;
      }
      if (this.selectRecords.length === 0) {
        this.isAllChecked = false;
      }
      this.$emit('chooseCheck', records);
    },
    /**
     * @description: 已选中条数前勾选框勾选方法
     * @param {type}
     * @return:
     * @author: YZZ
     */
    changeAllEvent() {
      // 表格全部勾选
      this.$refs[this.tableName + 'Table'].setAllCheckboxRow(this.isAllChecked);
      // 获取勾选的数据
      this.selectRecords = this.$refs[
        this.tableName + 'Table'
      ].getCheckboxRecords();
      if (this.selectRecords.length === 0) {
        this.isAllChecked = false;
      }
      this.$emit('chooseCheck', this.selectRecords);
    },
    /**
     * @description: 数据导出
     * @param fileName 文件名称
     * @param data 导出数据
     * @param columns 自定义表头
     * @return: 无
     * @author: ZJM
     */
    exportData(fileName, data, columns) {
      console.log(columns);

      const option = {
        isColgroup: true,
        filename: fileName,
        useStyle: true,
        // original: true,
        type: 'xlsx',
        // type: 'csv',
        // isHeader: true,
        // isFooter: true,
        columns: columns.filter(column => {
          console.log(column);
          // 在这里设置要保留的列条件
          return column.field !== '';
        }),
        data: data
      };
      // if (columns) {
      //   option.columnFilterMethod = (column) =>
      //     columns.includes(column.property);
      // }
      // console.log(data);
      this.$refs[this.tableName + 'Table'].exportData(option);
    },
    exportDatax(fileName, data, columns) {
      console.log(columns);

      const option = {
        isColgroup: true,
        filename: fileName,
        useStyle: true,
        // original: true,
        // type: 'xlsx',
        type: 'csv',
        // isHeader: true,
        // isFooter: true,
        // columns: columns.filter(column => {
        //   console.log(column);
        //   // 在这里设置要保留的列条件
        //   return column.field !== '';
        // }),
        data: data
      };
      if (columns) {
        option.columnFilterMethod = (column) =>
          columns.includes(column.property);
      }
      // console.log(data);
      this.$refs[this.tableName + 'Table'].exportData(option);
    },
    /**
     * @description: 点击行操作
     * @return: 无
     * @author: Lois
     */
    cellClickEvent({ row }) {
      this.$emit('clickRows', row);
    },
    /**
     * @description: 点击行操作
     * @return: 无
     * @author: Lois
     */
    cellDBLClickEvent({ row }) {
      this.$emit('dbclickRows', row);
    },
    /**
     * @description: 点击行选中事件
     * @param row 行数据
     * @return: 无
     * @author: ZJM
     */
    currentChangeEvent({ row }) {
      const index = this.$refs[this.tableName + 'Table'].$getRowIndex(row);
      this.$emit('currentChangeEvent', row, index);
    },
    /**
     * @description: 设置高亮
     * @param index 行下标
     * @return: 无
     * @author: ZJM
     */
    setCurrentRow(index) {
      this.$refs[this.tableName + 'Table'].setCurrentRow(
        this.tableDatas[index]
      );
    },
    /**
     * @description: 取消高亮
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    clearCurrentRow() {
      this.$refs[this.tableName + 'Table'].clearCurrentRow();
    },
    /**
     * @description: 设置checkbox选中行
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    toggleCheckboxRow(row) {
      this.$refs[this.tableName + 'Table'].toggleCheckboxRow(row);
    },
    /**
     * @description: 取消checkbox选中行
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    clearCheckboxRow() {
      this.$refs[this.tableName + 'Table'].clearCheckboxRow();
    },
    /**
     * @description: 获取checkbox选中行
     * @param 无
     * @return: 选中数据
     * @author: ZJM
     */
    getCheckboxRecords() {
      return this.$refs[this.tableName + 'Table'].getCheckboxRecords();
    },
    /**
     * @description: 获取radio选中行
     * @param 无
     * @return: 选中数据
     * @author: ZJM
     */
    getRadioRecord() {
      return this.$refs[this.tableName + 'Table'].getRadioRecord();
    },
    setCheckboxRow(row, chekcked) {
      return this.$refs[this.tableName + 'Table'].setCheckboxRow(row, chekcked);
    },
    mergeRowMethod({ row, $rowIndex, column, data }) {
      const fields = this.mergeArr;
      if (this.mergeArr.length > 0) {
        var cellValue = row[column.property];
        if (cellValue && fields.includes(column.property)) {
          var firstCellValue = row[fields[0]];
          var prevRow = data[$rowIndex - 1];
          let nextRow = data[$rowIndex + 1];
          if (prevRow && prevRow[column.property] === cellValue && prevRow[fields[0]] === firstCellValue) {
            return { rowspan: 0, colspan: 0 };
          } else {
            let countRowspan = 1;
            while (nextRow && nextRow[column.property] === cellValue && nextRow[fields[0]] === firstCellValue) {
              nextRow = data[++countRowspan + $rowIndex];
            }
            if (countRowspan > 1) {
              return { rowspan: countRowspan, colspan: 1 };
            }
          }
        }
      }
    }
  }
};
</script>

<style lang="less" scoped>
// 表格下部分左边checkbox，选中条数，按钮样式
.page-left {
  position: absolute;
  left: 0px;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
  display: flex;
  align-items: center;
  // transform: translateX(-22.5%);
  // text-align: left;
  // display: flex;
  // align-items: center;
  // margin-top: 0px;
}

// checkbox样式
.tableAllCheck {
  width: 47px;
  display: flex;
  justify-content: center;
  border-right: 1px solid #e8eaec;
  margin-left: 2px;
}

// 选中条数和checkbox左边距，选中条数和按钮右边距
.select-count {
  margin: 0 10px;
}
</style>
