import { postAplicationJSON } from '@/utils/request';

// 获取状态设备的查询条件
export const GetStatusEquipmentQueryParameter = (param, jsonData) =>
  postAplicationJSON('webReportStatistics/getDeviceStateReportStatisticsQueryParameter.action?', param, jsonData);

// 获取状态设备统计报表
export const GetCheckStatisticalWebReport = (param, jsonData) =>
  postAplicationJSON('webReportStatistics/getDeviceStateReportStatistics.action?', param, jsonData);

// 获取安灯状态设备统计报表
export const GetAndonCheckStatisticalWebReport = (param, jsonData) =>
  postAplicationJSON('existFunctionUpdate/getDeviceStateReportStatistics.action?', param, jsonData);

// 获取设备小时效率趋势
export const GetEfficiencyTrend = (param, jsonData) =>
  postAplicationJSON('webReportStatistics/getDeviceHoursEffTrend.action?', param, jsonData);

// 获取设备小时周期趋势
export const GetCycleTrend = (param, jsonData) =>
  postAplicationJSON('webReportStatistics/getDeviceHoursProductCycleTrend.action?', param, jsonData);

// 获取设备24小时运行状态
export const GetStatus24Hour = (param, jsonData) =>
  postAplicationJSON('deviceLive/getOneDayDetailStateClock.action?', param, jsonData);

// 历史设备状态
export const GetStatusRange = (param, jsonData) =>
  postAplicationJSON('webReportStatistics/deviceGanttChart.action?',
    param,
    jsonData
  );

// 设备空闲报表统计,平均加工空闲时间报表
export const deviceIdleGetIdleProductReport = (param, jsonData) =>
  postAplicationJSON('deviceIdle/getIdleProductReport.action?',
    param,
    jsonData
  );
// 产量生产周期统计报表
export const getProductionCycleStatistics = (param, jsonData) =>
  postAplicationJSON('webProductionStatistics/getProductionCycleStatistics.action?',
    param,
    jsonData
  );
// 获取配置根据枚举获取
export const configurationGetConfigurationByEnum = (param, jsonData) =>
  postAplicationJSON('configuration/getConfigurationByEnum.action?',
    param,
    jsonData
  );
