/**
 * @description: 车间-产线-设备树形【chlidren】
 * @param 无
 * @return:无
 * @author: ZJM
 */
export function deviceWorkshopLineDeviceGetTreeFunc() {
  return new Promise((resolve, reject) => {
    instance.post('deviceGroup/getAllGroupAndDevice.action')
      .then(res => {
        // var resData = { ...res.data.rows };
        for (var i = 0; i < res.data.rows.length; i++) {
          if (res.data.rows[i].code) {
            res.data.rows[i].id = res.data.rows[i].code;
          }
        }
        // setTimeout(() => {
        // console.log('666', res.data.rows);
        res.data.rows = dataFormatter(res.data.rows);
        resolve(res.data);
        // }, 300);
      })
      .catch(err => {
        reject(err.data);
      });
  });
}

