/**
 * @description: 返回数据格式化
 * @param data rows数据
 * @return: 格式化后数据
 * @author: ZJM
 */
export function dataFormatter(data) {
  var treeArray = data;
  for (var x in treeArray) {
    treeArray[x].value = treeArray[x].id;
    treeArray[x].label = treeArray[x].name;
  }
  var r = [];
  var tmpMap = {};
  for (var i = 0, l = treeArray.length; i < l; i++) {
    tmpMap[treeArray[i].id] = treeArray[i];
  }
  for (i = 0, l = treeArray.length; i < l; i++) {
    var key = tmpMap[treeArray[i].pId];
    if (key) {
      if (!key.children) {
        key.children = [];
        key.children.push(treeArray[i]);
      } else {
        key.children.push(treeArray[i]);
      }
    } else {
      r.push(treeArray[i]);
    }
  }
  return r;
};

