// 数据克隆
export function deepClone(obj) {
  const objClone = Array.isArray(obj) ? [] : {};
  if (obj && typeof obj === 'object') {
    for (var key in obj) {
      // eslint-disable-next-line no-prototype-builtins
      if (obj.hasOwnProperty(key)) {
        // 判断ojb子元素是否为对象，如果是，递归复制
        if (obj[key] && typeof obj[key] === 'object') {
          objClone[key] = this.deepClone(obj[key]);
        } else {
          // 如果不是，简单复制
          objClone[key] = obj[key];
        }
      }
    }
  }
  return objClone;
};

/**
 * @description: 将自定义配置混入默认表头设置
 * @param {array} customSetting 后端返回的自定义列配置
 * @param {array} defaultColumns 前端写的默认列配置
 * @return {type} array
 * @author: cyb
 */
export function mixDefaultAndCustomColumns(customSetting, defaultColumns) {
  const mixedColumns = [];
  if (Array.isArray(customSetting) && Array.isArray(defaultColumns)) {
    // 当自定义配置存在时
    if (customSetting.length > 0) {
      for (const item of customSetting) {
        // 找到默认的列
        const defaultColumn = defaultColumns.find(
          column => column.field === item.field
        );

        if (defaultColumn) {
          // 在默认列中混入自定义配置
          const mixedColumn = Object.assign({}, defaultColumn, {
            minWidth: item.width,
            visible: item.visible,
            required: item.required
          });

          mixedColumns.push(mixedColumn);
        }
      }

      // 如果第一列是checkbox，则加入
      if (defaultColumns[1].type && defaultColumns[1].type === 'checkbox') {
        mixedColumns.unshift(defaultColumns[1]);
      }
      // 如果第一列是checkbox，则加入
      if (defaultColumns[0].type && defaultColumns[0].type === 'radio') {
        mixedColumns.unshift(defaultColumns[0]);
      }
      // 如果第一列是checkbox，则加入
      if (defaultColumns[0].type && defaultColumns[0].type === 'checkbox') {
        mixedColumns.unshift(defaultColumns[0]);
      }

      console.log('7788', customSetting, defaultColumns, mixedColumns);
      for (var i = 0; i < defaultColumns.length; i++) {
        if (defaultColumns[i].eType) {
          var flag = 0;
          for (var j = 0; j < customSetting.length; j++) {
            if (defaultColumns[i].field === customSetting[j].field) {
              flag += 1;
            }
          }
          if (flag === 0) {
            mixedColumns.push(defaultColumns[i]);
          }
        }
      }

      const length = defaultColumns.length;
      // 如果最后一列是操作，则加入
      if (
        defaultColumns[length - 1].title &&
        defaultColumns[length - 1].title === '操作'
      ) {
        mixedColumns.push(defaultColumns[length - 1]);
      }
      console.log('9999', mixedColumns);
      return mixedColumns;
    } else {
      return defaultColumns;
    }
  } else {
    return [];
  }
}

