/**
 * xlsx 导出所需的数据
 * @param { Array } filterKeys
 * @param { Array } jsonData
 */

// Format and filter json data using filterKeys array
export function formatJson(filterKeys, jsonData) {
  return jsonData.map((data, index) => filterKeys.map(key => {
    if (data[key] == null || data[key] === '') {
      return '-';
    } else {
      return data[key];
    }
  }));
}

// 格式转换
export function getTimeFunc(param) {
  var date = new Date(param);
  var y = date.getFullYear(); // 获取年份
  var m = date.getMonth() + 1; // 获取月份
  var d = date.getDate(); // 获取当月最后一日
  m = m < 10 ? '0' + m : m; // 月份补 0
  d = d < 10 ? '0' + d : d; // 日数补 0

  return [y, m, d].join('-');
}

export function getTodayFirstDay(param) {
  var date = new Date(param);
  var y = date.getFullYear(); // 获取年份
  var m = date.getMonth() + 1; // 获取月份
  var d = date.getDate(); // 获取当月最后一日
  m = m < 10 ? '0' + m : m; // 月份补 0
  d = d < 10 ? '0' + d : d; // 日数补 0

  return [y, m, d].join('-') + ' 00:00:00';
}

// 当日最晚时间
export function getTodayLastDay(param) {
  var date = new Date(param);
  var y = date.getFullYear(); // 获取年份
  var m = date.getMonth() + 1; // 获取月份
  var d = date.getDate(); // 获取当月最后一日
  m = m < 10 ? '0' + m : m; // 月份补 0
  d = d < 10 ? '0' + d : d; // 日数补 0

  return [y, m, d].join('-') + ' 23:59:59';
}
