<template>
  <div class="operation-item" @click="onOprationItem">
    <div class="operation-icon">
      <slot name="operation-icon"></slot>
      <!-- <img :src="require('../../../assets/img/Currency/common_button_xiangqing.png')" alt=""> -->
    </div>
    <div class="operation-span">
      <!-- <span>查看操作记录</span> -->
      <slot name="operation-name"></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    name: {
      type: String,
      required: true
    }
  },
  methods: {
    onOprationItem() {
      this.$emit('onOprationItem', this.name);
    }
  }
};
</script>

<style lang="less" scoped>
.operation-item {
  display: flex;
  align-items: center;
  justify-content : flex-end;
  float: right;
}

.operation-icon {
  display: flex;
  align-items: center;
  img {
    width: 30px;
    height: 30px;
  }
}

.operation-span {
  margin-right: 19px;
  margin-left: 5px;
  letter-spacing: 0px;
  span {
    color: #000000;
  }
}

.operation-item:hover {
  background: #f3f3f3;
  cursor: pointer;
}
</style>
